.section5 {
  display: flex;
  flex-direction: column;
  align-items: center;

  margin-top: 224px;
  max-width: 1785.11px;
}

.articles {
  display: flex;
  flex-direction: row;
  margin-bottom: 69px;
}

.article {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 16px;
  margin-right: 16px;
  width: 350px;
  background: #ffffff;
  box-shadow: 10px 40px 50px rgba(229, 233, 246, 0.4);
  border-radius: 20px;
}

.articleTitle {
  margin-top: 24px;
  margin-bottom: 12px;
  padding-left: 33px;
  padding-right: 51px;
  font-family: Mulish;
  font-style: normal;
  font-weight: bold;
  font-size: 21px;
  line-height: 32px;
  color: #000000;
  mix-blend-mode: normal;
}

.articleText {
  padding-left: 33px;
  padding-right: 51px;
  font-family: Mulish;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 28px;
  color: #7d7987;
  mix-blend-mode: normal;
}

.moreButton {
  padding-left: 33px;
  padding-bottom: 35px;
  font-family: Mulish;
  font-style: normal;
  font-weight: 600;
  font-size: 17px;
  line-height: 28px;
  color: #4089ed;
  mix-blend-mode: normal;
  cursor: pointer;
}
